webpackJsonp([157313778885209],{

/***/ 529:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"Having a CMS for GatsbyJS - easier than ever!","date":"2018-01-19T00:00:00.000Z","tags":["JAMStack","JavaScript","GatsbyJS"]},"timeToRead":5,"html":"<h2 id=\"introduction\"><a href=\"#introduction\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Introduction</h2>\n<p>As I have previously mentioned, <a href=\"/admin-ui-gatsby-static-site-generator\">NetlifyCMS is one of the most flexible \"CMS\" applications</a> on the market at the moment. In the past you had to tweak both GatsbyJS and Netlify service configurations, so that the output of first was useful for the second. I shared about these details in a <a href=\"https://www.gatsbyjs.org/blog/2017-11-06-migrate-hugo-gatsby/#admin-panel\">story about migrating from Hugo to GatsbyJS</a>. Few weeks after these shared stories, Netlify released <a href=\"https://www.netlify.com/blog/2017/12/07/open-source-netlify-cms-hits-1.0-bringing-git-based-content-management-to-static-sites-everywhere/\">NetlifyCMS 1.0</a>. I think this release is a big milestone for the project because it involved a lot of work on <a href=\"https://github.com/netlify/netlify-cms/issues?q=is%3Aissue+is%3Aopen+sort%3Acomments-desc+label%3A%22area%3A+design%22\">design perspective</a> but also on authentication and integration parts which make the product easier to adopt.</p>\n<p>A bit more than a month after this release, I have the feeling not many know about the improvements available. I blame the holiday season of December :) And so in this blog post I'll share my personal impressions. I believe NetlifyCMS is still one of the best tools to have together with a static site generator, and it's also well-integrated with another great service - Netlify.</p>\n<h3 id=\"starting-a-project\"><a href=\"#starting-a-project\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Starting a project</h3>\n<p>When I published my thoughts on using NetlifyCMS and GatsbyJS together to make a modern web site with an admin panel, I started to get questions on twitter how I made a given project and how I organised a repository. My reply was always pointing to <a href=\"https://github.com/kalinchernev/kalinchernev.github.io\">the repository storing the github pages for my username</a>. A friendly way to say RTFM - the blog posts were the documentation I thought - just fork the repo and tweak it!</p>\n<p>Some people succeeded taking an example, others didn't. The ones who didn't, didn't because making websites can be the job of a site builders and not developers. Being a site builder with experience and good expectation management with the client is good. So, starting a project should be easy. A successful WCMS product sometimes means starting with a visually appealing base, and being able to involve developers, where and when necessary on a later stage. WordPress with wordpress.com is an example of that. Start small, build a prototype, demo it, get trust for more work on a project, succeed, that's what I mean :)</p>\n<p>And there's the first good news - starting with GatsbyJS and adding an administration panel hasn't been easier!</p>\n<ol>\n<li>Go and <a href=\"https://www.netlifycms.org/docs/start-with-a-template/\">select the right starter</a>:</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-b6852.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 720px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 49.4933749025721%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Select the GatsbyJS starter\"\n        title=\"\"\n        src=\"/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-6f08c.png\"\n        srcset=\"/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-c1ddb.png 180w,\n/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-aa93f.png 360w,\n/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-6f08c.png 720w,\n/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-78b35.png 1080w,\n/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-b741e.png 1440w,\n/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-bcf92.png 2160w,\n/static/netlifycms-gatsbyjs-starter-eb16966fb80a713b698da64c620b034a-b6852.png 2566w\"\n        sizes=\"(max-width: 720px) 100vw, 720px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"2\">\n<li>Give your project a name</li>\n</ol>\n<p><img src=\"/gatsbyjs-starter-progress-f5d9f6cb0aba54886c68678d9a3d81db.gif\" alt=\"Naming your project\"></p>\n<ol start=\"3\">\n<li>Accept invitation</li>\n</ol>\n<p>When you start the creation of a new project, the new identity service of Netlify will create your user 1, and will send you an invite for it, just accept it :)</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/accept-netlify-invite-bd87e499fde82ad4298b6f4dc77b5d7b-6fb26.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 720px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 35.76732673267327%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Accept Netlify invite\"\n        title=\"\"\n        src=\"/static/accept-netlify-invite-bd87e499fde82ad4298b6f4dc77b5d7b-6f08c.png\"\n        srcset=\"/static/accept-netlify-invite-bd87e499fde82ad4298b6f4dc77b5d7b-c1ddb.png 180w,\n/static/accept-netlify-invite-bd87e499fde82ad4298b6f4dc77b5d7b-aa93f.png 360w,\n/static/accept-netlify-invite-bd87e499fde82ad4298b6f4dc77b5d7b-6f08c.png 720w,\n/static/accept-netlify-invite-bd87e499fde82ad4298b6f4dc77b5d7b-78b35.png 1080w,\n/static/accept-netlify-invite-bd87e499fde82ad4298b6f4dc77b5d7b-b741e.png 1440w,\n/static/accept-netlify-invite-bd87e499fde82ad4298b6f4dc77b5d7b-6fb26.png 1616w\"\n        sizes=\"(max-width: 720px) 100vw, 720px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"4\">\n<li>Set credentials</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-9a59c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 720px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 58.0952380952381%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"NetlifyCMS accepting the invite and setting personal credentials\"\n        title=\"\"\n        src=\"/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-6f08c.png\"\n        srcset=\"/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-c1ddb.png 180w,\n/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-aa93f.png 360w,\n/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-6f08c.png 720w,\n/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-78b35.png 1080w,\n/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-b741e.png 1440w,\n/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-bcf92.png 2160w,\n/static/netlifycms-set-credentials-ad28900e6e18dedf56f042283daac745-9a59c.png 3360w\"\n        sizes=\"(max-width: 720px) 100vw, 720px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>If you want to invite others to contribute to your new website, or you want to change your email, you can use Netlify Identity console to manage users.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/netlify-identity-management-efd21bc58c13ff4e743a26d09c895f08-712d0.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 720px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 60.921366163621926%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Netlify identity management console\"\n        title=\"\"\n        src=\"/static/netlify-identity-management-efd21bc58c13ff4e743a26d09c895f08-6f08c.png\"\n        srcset=\"/static/netlify-identity-management-efd21bc58c13ff4e743a26d09c895f08-c1ddb.png 180w,\n/static/netlify-identity-management-efd21bc58c13ff4e743a26d09c895f08-aa93f.png 360w,\n/static/netlify-identity-management-efd21bc58c13ff4e743a26d09c895f08-6f08c.png 720w,\n/static/netlify-identity-management-efd21bc58c13ff4e743a26d09c895f08-78b35.png 1080w,\n/static/netlify-identity-management-efd21bc58c13ff4e743a26d09c895f08-712d0.png 1259w\"\n        sizes=\"(max-width: 720px) 100vw, 720px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"5\">\n<li>Enjoy your CMS :)</li>\n</ol>\n<p>You will be automatically redirected the administration pages of your new site! Enjoy! :)</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/netlifycms-admin-pages-1cca45673ad8ab2d04c10184429cbc4f-0d483.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 720px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 35.587431693989075%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"NetlifyCMS administration pages\"\n        title=\"\"\n        src=\"/static/netlifycms-admin-pages-1cca45673ad8ab2d04c10184429cbc4f-6f08c.png\"\n        srcset=\"/static/netlifycms-admin-pages-1cca45673ad8ab2d04c10184429cbc4f-c1ddb.png 180w,\n/static/netlifycms-admin-pages-1cca45673ad8ab2d04c10184429cbc4f-aa93f.png 360w,\n/static/netlifycms-admin-pages-1cca45673ad8ab2d04c10184429cbc4f-6f08c.png 720w,\n/static/netlifycms-admin-pages-1cca45673ad8ab2d04c10184429cbc4f-78b35.png 1080w,\n/static/netlifycms-admin-pages-1cca45673ad8ab2d04c10184429cbc4f-b741e.png 1440w,\n/static/netlifycms-admin-pages-1cca45673ad8ab2d04c10184429cbc4f-0d483.png 1464w\"\n        sizes=\"(max-width: 720px) 100vw, 720px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3 id=\"the-administration-panel\"><a href=\"#the-administration-panel\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>The administration panel</h3>\n<p>One would expect that going to the <a href=\"https://www.netlifycms.org/docs/\">official docs</a>, there would be a short overview or a tutorial about the features of the product. However, this is not the case, most probably because it's so simple :)</p>\n<h4 id=\"content-management\"><a href=\"#content-management\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Content management</h4>\n<p><img src=\"/netlifycms-create-blog-post-403d59530315f51be9197c2db7d2d819.gif\" alt=\"Create a post\"></p>\n<p>Clicking on \"Publish\" will put the content in the main branch of the repository and will trigger the build.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/netlify-build-settings-5c903d449b44ffc552f84a4f9c15aa87-ef08c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 720px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 74.818401937046%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Netlify build settings\"\n        title=\"\"\n        src=\"/static/netlify-build-settings-5c903d449b44ffc552f84a4f9c15aa87-6f08c.png\"\n        srcset=\"/static/netlify-build-settings-5c903d449b44ffc552f84a4f9c15aa87-c1ddb.png 180w,\n/static/netlify-build-settings-5c903d449b44ffc552f84a4f9c15aa87-aa93f.png 360w,\n/static/netlify-build-settings-5c903d449b44ffc552f84a4f9c15aa87-6f08c.png 720w,\n/static/netlify-build-settings-5c903d449b44ffc552f84a4f9c15aa87-78b35.png 1080w,\n/static/netlify-build-settings-5c903d449b44ffc552f84a4f9c15aa87-ef08c.png 1239w\"\n        sizes=\"(max-width: 720px) 100vw, 720px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>If you rather have an additional moderation step before content lands into production, you use the <a href=\"https://www.netlifycms.org/docs/configuration-options/#publish-mode\"><code class=\"language-text\">publish_mode</code></a>. For this, you'll need to change the code of the repository (the starter) created under your Github username.</p>\n<h4 id=\"other-settings\"><a href=\"#other-settings\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Other settings</h4>\n<p>Content management features being quite straight-forward, by default the starter provides defaults which are common sense. For more advanced features and configurations options in the administration pages, you'll need to <a href=\"https://www.netlifycms.org/docs/configuration-options/\">use the <code class=\"language-text\">config.yml</code></a> file in the repository as described in the docs.</p>\n<p>In terms of GatsbyJS, this file should always be placed in the <code class=\"language-text\">static/admin</code> folder in order to be accessible by <code class=\"language-text\">window.netlifyIdentity</code>. Documentation pages already describe <a href=\"https://www.gatsbyjs.org/docs/caching/#static-files\">how static files are handled</a>.</p>\n<h3 id=\"github\"><a href=\"#github\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Github</h3>\n<p>Although there's the NetlifyCMS for providing eye-candy and easy UI for content management, one can still use the \"regular\" content management workflows developers have - using the repository on Github to add new content and change existing content :)</p>\n<h4 id=\"deploying-changes\"><a href=\"#deploying-changes\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Deploying changes</h4>\n<p>Pushing changes to main branch (master by default) will trigger the Netlify service to run another build of the project:</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/netlify-listening-repository-changes-df05cb0a19279ee703310995b8c418e9-1bcb8.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 720px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 61.7124394184168%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Netlify build settings\"\n        title=\"\"\n        src=\"/static/netlify-listening-repository-changes-df05cb0a19279ee703310995b8c418e9-6f08c.png\"\n        srcset=\"/static/netlify-listening-repository-changes-df05cb0a19279ee703310995b8c418e9-c1ddb.png 180w,\n/static/netlify-listening-repository-changes-df05cb0a19279ee703310995b8c418e9-aa93f.png 360w,\n/static/netlify-listening-repository-changes-df05cb0a19279ee703310995b8c418e9-6f08c.png 720w,\n/static/netlify-listening-repository-changes-df05cb0a19279ee703310995b8c418e9-78b35.png 1080w,\n/static/netlify-listening-repository-changes-df05cb0a19279ee703310995b8c418e9-1bcb8.png 1238w\"\n        sizes=\"(max-width: 720px) 100vw, 720px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>This workflow is similar to Heroku or other PaaS solutions.</p>\n<h4 id=\"previewing-changes\"><a href=\"#previewing-changes\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Previewing changes</h4>\n<p>When moderation workflows are enabled, creating a draft from NetlifyCMS administration pages means that the tool will create a new pull request to the main branch on your behalf. You can do the same just as you are used to do working with the repository directly - creating a new branch locally and pushing it to a branch for a pull request.</p>\n<p><a href=\"https://www.netlify.com/blog/2016/07/20/introducing-deploy-previews-in-netlify/\">Netlify previews</a> are a popular useful feature <a href=\"https://github.com/gatsbyjs/gatsby/blob/936c33a262eba0d3e053e67396079c34507f5cb8/docs/docs/deploy-gatsby.md\">used by GatsbyJS project</a> and many others.</p>\n<h3 id=\"conclusions\"><a href=\"#conclusions\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Conclusions</h3>\n<p>I hope this short tutorial would be useful to those searching for an Admin UI for a static site generator, and most specifically GatsbyJS. The process has been greatly simplified which hopefully will further increase the adoption of the JAM stack.</p>\n<p>The Gatsby Starter is welcoming, easy to get a GatsbyJS site up and running, so why not use this opportunity?</p>"}},"pathContext":{"slug":"cms-gatsbyjs"}}

/***/ })

});


